@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  font-family: 'Montserrat', sans-serif;
}
body, html{
  overflow-x: hidden;
}
.primary{
  background-color: #161c51;
  color: white;
  padding: 5px 20px;
  border: none;
}

.layout {
    margin: 0 100px;
}

.header {
    background-color: #161c51;
    padding: 20px;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 28px;
    color: rgba(255, 255, 255, 0.716);
    margin: 0 !important;
    cursor: pointer;
}

.user {
    font-size: 15px;
    color: rgba(255, 255, 255, 0.716);
    margin: 0 !important;
}

.content {
    height: 85vh;
    box-shadow: 0 0 3px grey;
    margin-top: 20px;
    border-top-right-radius: 30px;
    border-top-left-radius: 30px;
    padding: 15px;
 overflow-y: scroll;

}

.spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.ant-spin-dot-item {
    background-color: gray !important;
}

@media screen and (max-width: 700px) {
    .layout {
        margin: 0 15px;

    }
}
.filters {
    box-shadow: 0 0 3px grey;
    padding: 15px 20px;
    border-radius: 10px;
}

.transcation-form label {
    color: rgba(0, 0, 0, 0.781) !important;
}

.transcation-form input {

    border: 1px solid gray;
    border-bottom: 1px solid gray;
    box-shadow: none !important;
}

.transcation-form input:focus {

    border: 1px solid gray;
    border-bottom: 1px solid gray !important;
    box-shadow: none !important;
}

.ant-select-selector {
    border: 1px solid gray !important;
}

.ant-select-selector:focus {
    box-shadow: none !important;
}
.toogle{
    border: 1px solid rgba(24, 24, 24, 0.523)!important;
    border-radius: 5px;
    padding: 5px 10px;
   
}
.anticon svg{
    font-size: 20px;
    cursor: pointer;
}
.active-icons{
    color: black;
}
.inactive-icons{
    color: gray;
}
 .ant-picker{
    border: 1px  solid rgba(16, 16, 16, 0.66) !important;
 }
 .ant-pagination-item a{
    color: rgb(32, 31, 31) !important;
 }
 @media screen and (max-width: 700px) {
    .homeHead {
        margin: 5px 5px;
        

    }
    .filters {
        box-shadow: 0 0 3px grey;
        padding: 15px 20px;
        border-radius: 10px;
     
        white-space: nowrap;
        word-wrap: break-word;
    }
}
.transaction-count{
    box-shadow: 0 0 2px gray;
    padding: 15px;
    border-radius: 10px;
    color: rgb(72, 71, 71) !important;
}
.analatics-cards h4{
 font-size: 20px;
 color: gray !important;
}
.analatics-cards h5{
    font-size: 16px;
    color: gray !important;
   }
.category-card{
    padding: 5px 15px;
    box-shadow: 0 0 2px gray;
    margin-top: 15px;
    border-radius: 10px;
}
.register{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to right, #30cfd0 0%, #330867 100%);
}
.lottie{
    height: 400px;
}
.register input{
    background-color: transparent;
    color: rgba(255, 255, 255, 0.806);
    border: none;
    border-bottom: 1px solid rgba(93, 92, 92, 0.719);
}
input:focus{
    outline: none !important;
    box-shadow: none !important;
}
label ,a {
    color: rgba(255, 255, 255, 0.527) !important;
}
.register h1{
    color: white;
}
